import * as React from 'react';
import { bindActionCreators } from 'redux';
import { connect } from 'react-redux';
import { Dispatch } from 'redux';
import { RootState } from '../../../root.reducer';
import { getSelectedClinicId } from '../../booking/forces/selectors';
import { changeSelectedClinicId, setBookingSection } from '../../booking/forces/actions';
import { BookingSectionIDs } from '../../booking/forces/types';
import { fetchAllClinics } from '../forces/actions';
import { getAllClinics } from '../forces/selectors';
import CallToActionButton, {
  CallToActionProps,
} from '../../../common/components/callToAction/CallToActionButton';
import styles from './ClinicSection.module.css';
import LoadingIndicator from '../../../common/components/loadingIndicator/LoadingIndicator';

interface OwnProps {
  selectedWaitlistId?: string | null;
}

interface State {
  selectedWaitlistId?: string | null;
  hasFetchedClinics: boolean;
}

type DispatchProps = ReturnType<typeof mapDispatchToProps>;
type StateProps = ReturnType<typeof mapStateToProps>;
type Props = DispatchProps & StateProps & OwnProps & React.HTMLAttributes<HTMLElement>;

class ClinicSection extends React.Component<Props, State> {
  constructor(props: Props) {
    super(props);
    this.state = {
      hasFetchedClinics: false,
    };
  }

  componentDidMount = () => {
    const hasFetchedClinics = this.state.hasFetchedClinics;
    if (!hasFetchedClinics) {
      this.props.fetchAllClinics();
      this.setState({ hasFetchedClinics: true });
    }
  };

  handleSelection = () => {
    this.props.setBookingSection(BookingSectionIDs.CalendarSection);
  };

  selectClinic = (clinicId: string) => {
    this.props.changeSelectedClinicId(clinicId);
    this.handleSelection();
  };

  renderClinicButtons = (selectedClinicId: string | null) => {
    const allClinics = this.props.allClinics;
    return allClinics.map(clinic => (
      <CallToActionButton
        key={clinic.id}
        styleType={clinic.id === selectedClinicId ? 'secondary' : 'outlined'}
        theme="light"
        size="normal"
        className={styles.locationButton}
        onClick={() => this.selectClinic(clinic.id)}>
        {clinic.name}
      </CallToActionButton>
    ));
  };

  render() {
    if (!this.state.hasFetchedClinics) {
      return <LoadingIndicator />;
    }
    const selectedClinicId = this.props.selectedClinicId ? this.props.selectedClinicId : null;
    return (
      <div className={styles.container}>
        <div className={styles.divider} />
        <div className={styles.locationButtonContainer}>
          {this.renderClinicButtons(selectedClinicId)}
        </div>
      </div>
    );
  }
}

const mapStateToProps = (state: RootState) => ({
  selectedClinicId: getSelectedClinicId(state),
  allClinics: getAllClinics(state),
});

const mapDispatchToProps = (dispatch: Dispatch) =>
  bindActionCreators(
    {
      changeSelectedClinicId,
      setBookingSection,
      fetchAllClinics,
    },
    dispatch
  );

export default connect<StateProps, DispatchProps, OwnProps, RootState>(
  mapStateToProps,
  mapDispatchToProps
)(ClinicSection);
