import * as React from 'react';
import classNames from 'classnames';
import { bindActionCreators, Dispatch } from 'redux';
import { connect } from 'react-redux';
import { navigate } from 'gatsby';
import { H1, BodyText, H3 } from '../../../common/components/texts';
import CallToActionLink from '../../../common/components/callToAction/CallToActionLink';
import Routes from '../../../common/routing/Routes';
import { RootState } from '../../../root.reducer';
import { RouteComponentProps } from '@reach/router';
import {
  getSelectedTime,
  getSelectedDate,
  getEditMode,
  getSelectedClinicId,
} from '../forces/selectors';
import { formatToDateAndYear, formatDurationToTime } from '../../../utils/date.utils';
import Layout from '../../../common/components/layout/Layout';
import BookingOlioPointsGuestInformation from '../components/BookingOlioPointsGuestInformation';
import { Column, Container, Row } from '../../../common/components/grid/Grid';
import { isLoggedIn } from '../../user/forces/selectors';
import { getSelectedClinic } from '../../clinics/forces/selectors';
import styles from './BookingSuccess.module.css';

interface OwnProps {}
type DispatchProps = ReturnType<typeof mapDispatchToProps>;
type StateProps = ReturnType<typeof mapStateToProps>;
type Props = DispatchProps &
  StateProps &
  OwnProps &
  RouteComponentProps &
  React.HTMLAttributes<HTMLElement>;

class BookingSuccess extends React.Component<Props> {
  componentDidMount() {
    if (this.props.selectedDate == null || this.props.selectedTime == null) {
      navigate(Routes.Main());
    }
  }

  generateBookingText({
    date,
    time,
    location,
    editMode,
  }: {
    date: string;
    time: string;
    location: string;
    editMode: boolean;
  }) {
    const baseText = {
      header: ['Tusen takk!'],
      body: `Vi gleder oss til  ta deg imot ${date} klokken ${''} ${time} p ${location} :)`,
    };
    if (editMode) {
      baseText.header.push('Bookingen din er oppdatert.');
    }
    return baseText;
  }

  getTexts({
    selectedTime,
    selectedDate,
    location,
    editMode,
  }: {
    selectedTime: number;
    selectedDate: number;
    location: string;
    editMode: boolean;
  }) {
    const date = formatToDateAndYear(selectedDate);
    const time = formatDurationToTime(selectedTime);
    return this.generateBookingText({ date, time, location, editMode });
  }

  generateCoronaWarningText = () => {
    return (
      <>
        <H3>Smitteverntiltak Covid-19</H3>
        <p>
          Helsen og sikkerheten til vre kunder og ansatte er vr hyeste prioritet. Med veiledning
          fra helsemyndighetene overholder vi alle anbefalte smitteverntiltak.
        </p>
        <b>Vi kan derfor ikke behande kunder som har:</b>
        <div className={styles.warningListContainer}>
          <ul className={styles.warningList}>
            <li>
              Mindre og strre symptomer p luftveisinfeksjon, f.eks feber, hoste, sr hals, eller
              pustevansker
            </li>
            <li>Vrt i nrkontakt med en person som har ftt pvist Covid-19 de siste 14 dagene</li>
            <li>Vrt utenlands i lpet av de siste 14 dagene</li>
          </ul>
        </div>
      </>
    );
  };

  render() {
    const {
      className,
      selectedTime,
      selectedDate,
      selectedClinic,
      isLoggedIn,
      editMode,
    } = this.props;
    const location = selectedClinic
      ? `${selectedClinic.name} (${selectedClinic.address.streetAddress})`
      : '';
    if (selectedDate == null || selectedTime == null) {
      return null;
    }
    const texts = this.getTexts({ selectedTime, selectedDate, location, editMode });
    const coronaWarningText = this.generateCoronaWarningText();
    return (
      <Layout className={classNames(styles.container, className)}>
        <Container>
          <Row>
            <Column xs={12} md={6} mdOffset={3} lg={4} lgOffset={4}>
              <H1 className={styles.title}>
                {texts.header[0]}
                <br />
                {texts.header[1]}
                <br />
                {texts.header[2]}
              </H1>
              <BodyText>{texts.body}</BodyText>

              {isLoggedIn ? null : (
                <BookingOlioPointsGuestInformation className={styles.guestInformation} />
              )}
            </Column>
          </Row>
          <Row>
            <Column xs={12} md={12} lg={12}>
              <BodyText>{coronaWarningText}</BodyText>
              <CallToActionLink
                styleType="outlined"
                theme="dark"
                to={Routes.Main()}
                className={styles.homeButton}>
                Hjem
              </CallToActionLink>
            </Column>
          </Row>
        </Container>
      </Layout>
    );
  }
}

const mapStateToProps = (state: RootState) => {
  const selectedClinicId = getSelectedClinicId(state);
  return {
    editMode: getEditMode(state),
    selectedDate: getSelectedDate(state),
    selectedTime: getSelectedTime(state),
    isLoggedIn: isLoggedIn(state),
    selectedClinic: getSelectedClinic(state, selectedClinicId),
  };
};

const mapDispatchToProps = (dispatch: Dispatch) => bindActionCreators({}, dispatch);

export default connect<StateProps, DispatchProps, OwnProps, RootState>(
  mapStateToProps,
  mapDispatchToProps
)(BookingSuccess);
